import React, { useState } from "react";

const FlashcardGenerator = () => {

    const [topic, setTopic] = useState('');
    const [flashcards, setFlashcards] = useState([]);
    const [selectedDataCard, setSelectedDataCard] = useState('');

    const handleChange = (event) => {
        setTopic(event.target.value);
    };

    const handleSubmit = async (event) => {
        event.preventDefault();

        if (!topic) {
            return;
        }

        try {
            console.log('wefwaekfbhj');
            // Use OpenAI API to generate questions
            // Replace 'YOUR_API_KEY' with your actual OpenAI API key
            const response = await fetch('https://api.openai.com/v1/chat/completions', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: 'Bearer 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',
                },
                body: JSON.stringify({
                    model: 'gpt-4',
                    messages: [
                        { role: 'system', content: `You are a researcher studying ${topic}.` },
                        { role: 'system', content: 'Q: What are some common questions asked about this topic?' },
                    ],
                    max_tokens: 30,
                    n: 1,
                    stop: ['\n'],
                }),
            });

            const data = await response.json();
            const questions = data.choices.map((choice) => choice.message.content);
            const uniqueQuestions = [...new Set(questions)]; // Filter out duplicate questions

            const newFlashcards = uniqueQuestions.map((question, index) => ({
                id: index,
                question: question,
                answer: '',
                showAnswer: true,
            }));

            setFlashcards(newFlashcards);
            setSelectedDataCard('');
        } catch (error) {
            console.error(error);
        }
    };

    const handleShowAnswer = (id) => {
        setFlashcards((prevFlashcards) =>
            prevFlashcards.map((flashcard) =>
                flashcard.id === id ? { ...flashcard, showAnswer: true } : flashcard
            )
        );
    };

    const handleDataCardClick = (title) => {
        setSelectedDataCard(title);
        setTopic(title);
    };
    return (
        <div className="container">
            <h1 className="text-center mt-5 mb-3">Flashcard Generator</h1>
            <form onSubmit={handleSubmit}>
                <div className="input-group mb-3">
                    <input
                        type="text"
                        className="form-control"
                        placeholder="Enter topic"
                        value={topic}
                        onChange={handleChange}
                    />
                    <button className="btn btn-primary" type="submit">
                        Generate Flashcards
                    </button>
                </div>
            </form>
            <div className="row">
                <div className="col-md-4">
                    <div
                        className={`card data-card bg-blue ${selectedDataCard === 'Artificial Intelligence (AI)' ? 'selected' : ''}`}
                        onClick={() => handleDataCardClick('Artificial Intelligence (AI)')}
                    >
                        <div className="card-body">
                            <h5 className="card-title">Artificial Intelligence (AI)</h5>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div
                        className={`card data-card bg-electric-blue ${selectedDataCard === 'Machine Learning (ML)' ? 'selected' : ''}`}
                        onClick={() => handleDataCardClick('Machine Learning (ML)')}
                    >
                        <div className="card-body">
                            <h5 className="card-title">Machine Learning (ML)</h5>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div
                        className={`card data-card bg-cyan ${selectedDataCard === 'Natural Language Processing (NLP)' ? 'selected' : ''}`}
                        onClick={() => handleDataCardClick('Natural Language Processing (NLP)')}
                    >
                        <div className="card-body">
                            <h5 className="card-title">Natural Language Processing (NLP)</h5>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-4">
                    <div
                        className={`card data-card bg-purple ${selectedDataCard === 'Deep Learning' ? 'selected' : ''}`}
                        onClick={() => handleDataCardClick('Deep Learning')}
                    >
                        <div className="card-body">
                            <h5 className="card-title">Deep Learning</h5>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div
                        className={`card data-card bg-indigo ${selectedDataCard === 'Computer Vision' ? 'selected' : ''}`}
                        onClick={() => handleDataCardClick('Computer Vision')}
                    >
                        <div className="card-body">
                            <h5 className="card-title">Computer Vision</h5>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div
                        className={`card data-card bg-teal ${selectedDataCard === 'Explainable AI' ? 'selected' : ''}`}
                        onClick={() => handleDataCardClick('Explainable AI')}
                    >
                        <div className="card-body">
                            <h5 className="card-title">Explainable AI</h5>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-4">
                    <div
                        className={`card data-card bg-pink ${selectedDataCard === 'Edge AI' ? 'selected' : ''}`}
                        onClick={() => handleDataCardClick('Edge AI')}
                    >
                        <div className="card-body">
                            <h5 className="card-title">Edge AI</h5>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div
                        className={`card data-card bg-yellow ${selectedDataCard === 'Quantum Machine Learning' ? 'selected' : ''}`}
                        onClick={() => handleDataCardClick('Quantum Machine Learning')}
                    >
                        <div className="card-body">
                            <h5 className="card-title">Quantum Machine Learning</h5>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div
                        className={`card data-card bg-orange ${selectedDataCard === 'Reinforcement Learning' ? 'selected' : ''}`}
                        onClick={() => handleDataCardClick('Reinforcement Learning')}
                    >
                        <div className="card-body">
                            <h5 className="card-title">Reinforcement Learning</h5>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-4">
                    <div
                        className={`card data-card bg-red ${selectedDataCard === 'Responsible AI' ? 'selected' : ''}`}
                        onClick={() => handleDataCardClick('Responsible AI')}
                    >
                        <div className="card-body">
                            <h5 className="card-title">Responsible AI</h5>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div
                        className={`card data-card bg-gray ${selectedDataCard === 'Trustworthy AI' ? 'selected' : ''}`}
                        onClick={() => handleDataCardClick('Trustworthy AI')}
                    >
                        <div className="card-body">
                            <h5 className="card-title">Trustworthy AI</h5>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div
                        className={`card data-card bg-green ${selectedDataCard === 'Swarm Intelligence' ? 'selected' : ''}`}
                        onClick={() => handleDataCardClick('Swarm Intelligence')}
                    >
                        <div className="card-body">
                            <h5 className="card-title">Swarm Intelligence</h5>
                        </div>
                    </div>
                </div>
            </div>
            <div id="flashcards-container">
                {flashcards.map((flashcard) => (
                    <div className={` flip-card ${flashcard.showAnswer ? 'flip' : ''}`} key={flashcard.id}>
                        <div className="flip-card-inner">
                            <div className="flip-card-front">
                                <h5 className="card-title">Question {flashcard.id + 1}</h5>
                                <p className="card-text">{flashcard.question}</p>
                            </div>
                            {flashcard.showAnswer && (
                                    <div className="flip-card-back">
                                        <h5 className="card-title">Answer {flashcard.id + 1}</h5>
                                        <p className="card-text">{"Artificial intelligence is the simulation of human intelligence processes by machines, especially computer systems. "}</p>
                                    </div>
                            )}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default FlashcardGenerator;