import React, { useEffect, useState } from 'react';
import SignIn from '../components/SignIn';

import { useNavigate } from "react-router-dom"
import Cookies from 'js-cookie';

const SignInScreen = () => {
    const [data, setData] = useState()
    const navigate = useNavigate()
    const handleSignIn = async (e, email, password) => {
        e.preventDefault();
        try {
            const response = await fetch('https://adiamant-server-production.up.railway.app/auth/login', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ email, password }),
            });

            if (response.ok) {
                const data = await response.json();
                setData(data)
                console.log(data);
                Cookies.set('user-data', JSON.stringify({ data }), { expires: 7 });
                console.log('Sign-in successful:', data);
            } else {
                console.log('Sign-in failed');
            }
        } catch (error) {
            console.error('Error:', error);
        }
    };
    useEffect(() => {
        if (data !== undefined) {
            navigate("/", { state: { token: data.token, user: data.user } })
        }
    }, [data])


    return (
        <div>
            <SignIn handleSignIn={handleSignIn} />
        </div>
    );
};

export default SignInScreen;
